.hairline-remove-right-bottom {
  border-bottom: 0;
}
.hairline-remove-right-bottom:after {
  display: none;
}
.hairline-remove-right-bottom-bak:after {
  display: none;
}
.hairline-remove-left-top:before {
  display: none;
}
.am-switch {
  display: inline-block;
  vertical-align: middle;
  width: 102px;
  height: 62px;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
  align-self: center;
}
.am-switch .checkbox {
  width: 102px;
  height: 62px;
  border-radius: 62px;
  box-sizing: border-box;
  background: #e5e5e5;
  z-index: 0;
  margin: 0;
  padding: 0;
  appearance: none;
  border: 0;
  cursor: pointer;
  position: relative;
  transition: all 300ms;
}
.am-switch .checkbox:before {
  content: ' ';
  position: absolute;
  left: 3px;
  top: 3px;
  width: 96px;
  height: 56px;
  border-radius: 56px;
  box-sizing: border-box;
  background: #fff;
  z-index: 1;
  transition: all 200ms;
  transform: scale(1);
}
.am-switch .checkbox:after {
  content: ' ';
  height: 56px;
  width: 56px;
  border-radius: 56px;
  background: #fff;
  position: absolute;
  z-index: 2;
  left: 3px;
  top: 3px;
  transform: translateX(0px);
  transition: all 200ms;
  -webkit-box-shadow: 4px 4px 8px rgba(100, 100, 100, 0.2);
}
.am-switch input[type="checkbox"] {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  border: 0 none;
  appearance: none;
}
.am-switch input[type="checkbox"]:checked + .checkbox {
  background: #4dd865;
}
.am-switch input[type="checkbox"]:checked + .checkbox:before {
  transform: scale(0);
}
.am-switch input[type="checkbox"]:checked + .checkbox:after {
  transform: translateX(40px);
}
.am-switch input[type="checkbox"]:disabled + .checkbox {
  opacity: 0.3;
}



