.hairline-remove-right-bottom {
  border-bottom: 0;
}
.hairline-remove-right-bottom:after {
  display: none;
}
.hairline-remove-right-bottom-bak:after {
  display: none;
}
.hairline-remove-left-top:before {
  display: none;
}
.am-segment {
  display: flex;
  background-color: #fff;
  border-width: 2px;
  border-style: solid;
  border-radius: 10px;
  border-color: #108ee9;
  overflow: hidden;
  margin: 0 30px;
}
.am-segment-item {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #108ee9;
  font-size: 26px;
  border-right: 2px solid #108ee9;
  line-height: 1;
  min-height: 54px;
}
.am-segment-item:last-child {
  border-right: 0;
}
.am-segment-item-selected {
  background: #108ee9;
  color: #fff;
}



